.top-pane {
  background-color: rgb(80, 76, 71);
}

.pane {
  height: 50vh;
  display: flex;
}
.editor-container {
  flex-grow: 1;
  flex-basis: 0;
  display: flex;
  flex-direction: column;
  padding: 8px;
  background-color: rgb(80, 76, 71);
}
.editor-title {
  display: flex;
  justify-content: space-between;
  background-color: #1b1616f1;
  color: white;
  padding: 8px 8px 8px 16px;
  border-top-right-radius: 8px;
  border-top-left-radius: 8px;
}
.CodeMirror {
  height: 100% !important;
}

.code-mirror-wrapper {
  flex-grow: 1;
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
  overflow: hidden;
}

.editor-container.collapsed {
  flex-grow: 0;
}
.editor-container.collapsed .CodeMirror-scroll {
  position: absolute;
}

.btns {
  display: flex;
}
.btns button {
  margin-left: 6px;
  border-radius: 4px;
}
.btns button.clear {
  color: rgb(4, 1, 1);
  font-size: 12px;
  font-family: Arial, Helvetica, sans-serif;
  background-color: rgb(119, 111, 111);
}
img {
  width: 23px;
  height: 23px;
}
.logo {
  display: flex;
  align-items: center;
}
.logo img {
  margin-right: 10px;
}
